* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  padding: 0;
}

.country-list {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.country-item {
  display: flex;
  gap: 0.7rem;
}

.country-title {
  display: flex;
  align-items: center;
  gap: 1rem
}

.country-desc > span {
  font-weight: 700;
}